* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 16px;
  line-height: 1.3;
}

html *,
body * {
  font-family: "Pretendard", sans-serif;
}

a,
button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.05);
}

.button-wrapper {
  width: 100%;
}

.btn-loan-add {
  display: block;
  border: 1px solid #D1D5DB;
  border-radius: 8px;
  width: 100%;
  font-size: 14px;
  color: #6B7280;
  padding: 12px 24px;
  position: relative;
  font-weight: 700;
}

.btn-loan-add:before {
  content: "";
  display: inline-block;
  vertical-align: top;
  margin: 1px 4px 1px 0;
  height: 16px;
  width: 16px;
  background: url(../img/theme/apthefin/mv_home_icon05.png) no-repeat 0 0;
  background-size: cover;
}

.mv-home {
  background: #F3F4F6;
}

.mv-home header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 12px 0 20px;
  background: #F3F4F6;
  z-index: 99;
}

.mv-home header .logo {
  width: 87px;
}

.mv-home header .logo img {
  max-width: 100%;
}

.mv-home header .total-menu {
  background: url(../img/theme/apthefin/bt_menu.svg) no-repeat 0 0;
  height: 32px;
  width: 32px;
}

.mv-home .container {
  height: 100vh;
  width: 100%;
  padding: 50px 24px 100px;
  overflow: auto;
}

.mv-home .mv-home-cont-box {
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  padding: 20px;
  background: #fff;
}

.mv-home .mv-home-cont-box:first-of-type {
  margin-top: 16px;
}

.mv-home .mv-home-cont-box.loan-con {
  padding: 0;
}

.mv-home .mv-home-cont-box.loan-con .loan-con-txt {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  height: 106px;
  background: url(../img/theme/apthefin/mv_home_loan_back.png) no-repeat 90% bottom, linear-gradient(274deg, #1C64F2 0%, #3F83F8 62.13%, #76A9FA 101.95%);
  border-radius: 16px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  display: flex;
  align-items: center;
  padding: 20px;
}

.mv-home .mv-home-cont-box.loan-con .loan-con-kind {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 64px;
  font-weight: 600;
  background: url(../img/theme/apthefin/mv_home_verLine.png) no-repeat 50% 50%;
}

.mv-home .mv-home-cont-box.loan-con .loan-con-kind a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 100%;
  position: relative;
  padding-left: 30px;
}

.mv-home .mv-home-cont-box.loan-con .loan-con-kind a.credit:before {
  content: "";
  display: block;
  background: url(../img/theme/apthefin/mv_home_icon01.png) no-repeat 0 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-30px, -50%);
  width: 24px;
  height: 24px;
  background-size: 100%;
}

.mv-home .mv-home-cont-box.loan-con .loan-con-kind a.mortgage:before {
  content: "";
  display: block;
  background: url(../img/theme/apthefin/mv_home_icon02.png) no-repeat 0 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-45px, -50%);
  width: 24px;
  height: 24px;
  background-size: 100%;
}

.mv-home .mv-home-cont-box.score span {
  font-size: 14px;
  color: #17181A;
  font-weight: 600;
}

.mv-home .mv-home-cont-box.score .my-score,
.mv-home .mv-home-cont-box.score .my-balance,
.mv-home .mv-home-cont-box.score .nice {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mv-home .mv-home-cont-box.score .my-score {
  padding-bottom: 16px;
  border-bottom: 1px solid #E5E7EB;
}

.mv-home .mv-home-cont-box.score .my-score-title {
  position: relative;
  padding-left: 26px;
}

.mv-home .mv-home-cont-box.score .my-score-title:before {
  content: "";
  display: block;
  height: 24px;
  width: 24px;
  background: url(../img/theme/apthefin/mv_home_icon03.png) no-repeat 0 0;
  background-size: cover;
  position: absolute;
  left: 0;
  top: -2px;
}

.mv-home .mv-home-cont-box.score .my-balance {
  padding-top: 16px;
}

.mv-home .mv-home-cont-box.score .my-balance-title {
  position: relative;
  padding-left: 26px;
}

.mv-home .mv-home-cont-box.score .my-balance-title:before {
  content: "";
  display: block;
  height: 20px;
  width: 20px;
  background: url(../img/theme/apthefin/mv_home_icon04.png) no-repeat 0 0;
  background-size: cover;
  position: absolute;
  left: 0;
  top: 0px;
}

.mv-home .mv-home-cont-box.score .nice {
  margin-top: 20px;
}

.mv-home .mv-home-cont-box.score .nice span {
  color: #6B7280;
  font-size: 12px;
}

.mv-home .mv-home-cont-box.score .mv-home-point-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #17181A;
  min-height: 24px;
  padding-right: 24px;
  background: url("../img/ic/keyboard_arrow_right.svg") no-repeat 100% 50%/24px 24px;
}

.mv-home .mv-home-cont-box.score .mv-home-point-link-sum {
  display: block;
  font-size: 16px;
  line-height: 1.625;
  font-weight: 700;
}

.mv-home .mv-home-cont-box.score .mv-home-point-link-unit {
  display: block;
  margin-left: 2px;
  font-size: 14px;
  line-height: 1.71429;
  font-weight: 700;
}

.mv-home .mv-home-cont-box.score .mv-home-point-link-text {
  display: block;
  font-size: 14px;
  line-height: 1.71429;
  font-weight: 600;
  color: #6B7280;
}

.mv-home .mv-home-cont-box.score .mv-home-loan-balance {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #17181A;
}

.mv-home .mv-home-cont-box.score .mv-home-loan-balance-sum {
  display: block;
  font-size: 16px;
  line-height: 1.625;
  font-weight: 700;
}

.mv-home .mv-home-cont-box.score .mv-home-loan-balance-unit {
  display: block;
  margin-left: 2px;
  font-size: 14px;
  line-height: 1.71429;
  font-weight: 700;
}

.mv-home .mv-home-cont-box.score .mv-home-loan-balance-text {
  display: block;
  font-size: 14px;
  line-height: 1.71429;
  font-weight: 600;
  color: #6B7280;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-tit {
  color: #17181A;
  font-weight: 700;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-tit a {
  color: inherit;
  display: block;
  background: url(../img/theme/apthefin/ic_arw_gray.png) no-repeat right 50%;
  background-size: 20px;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-tit a span {
  color: #1C64F2;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-listWrap {
  margin-top: 20px;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-listWrap + .button-wrapper {
  margin-top: 20px;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-list + .my-loan-list {
  margin-top: 24px;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-list .my-loan-list-con {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-list .my-loan-list-con .ico-bank {
  display: inline-block;
  margin-right: 10px;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-list .my-loan-list-con .ico-bank img {
  width: 32px;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-list .my-loan-list-con .my-loan-subject {
  font-size: 13px;
  font-weight: 700;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-list .my-loan-list-con .my-loan-money {
  font-weight: 700;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-list .my-loan-list-interest {
  width: 70px;
  text-align: right;
  margin-left: 5px;
  color: #1C64F2;
}

.mv-home .mv-home-cont-box.my-loan .my-loan-empty {
  margin: 0;
  font-size: 14px;
  line-height: 1.3;
  font-weight: 500;
  color: #6B7280;
  text-align: center;
  padding: 24px 0;
}

.mv-home .mv-home-cont-box.my-card a {
  background-image: url(../img/theme/apthefin/ic_arw_gray.png), url(../img/theme/apthefin/mv_home_icon06.png);
  background-repeat: no-repeat, no-repeat;
  background-position: right 50%, left 50%;
  background-size: 20px, 48px;
  display: block;
  padding-left: 48px;
}

.mv-home .mv-home-cont-box.my-card .my-card-tit {
  color: #17181A;
  font-weight: 600;
  letter-spacing: -1px;
}

.mv-home .mv-home-cont-box.my-card .my-card-desc {
  color: #6B7280;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -1px;
  line-height: 24px;
}

.mv-home .mv-home-cont-box.event {
  padding: 0;
  background: none;
  box-shadow: none;
}

.mv-home .mv-home-cont-box.event .slider {
  background: #E1EFFE !important;
}

.mv-home .mv-home-cont-box.event .slider .banner.inLink {
  height: 136px;
}

.mv-home .mv-home-cont-box.event .slider .banner.inLink a {
  background-color: #E1EFFE !important;
}

.mv-home .mv-home-cont-box.event .slider .banner.inLink a .banner_tit {
  padding-top: 13px;
  letter-spacing: -1px;
  color: #666;
}

.mv-home .mv-home-cont-box.event .slider .banner.inLink a .banner_tit .titPoint {
  letter-spacing: -2px;
}

.mv-home .mv-home-cont-box + .mv-home-cont-box {
  margin-top: 16px;
}